<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-container class="headerd">
          <a class="headlink"
             href="javascript:void(0)"
             @click="gotoHome">
            <img src="./assets/web-logo.png"
                 class="logo" />
            <img src="./assets/web-logos.png"
                 class="logos" />
          </a>
          <el-menu :default-active="this.$route.fullPath"
                   router-mode="horizontal"
                   class="el-menu-demo"
                   mode="horizontal"
                   :router="true"
                   @select="handleSelect">
            <el-menu-item index="/Home">首页</el-menu-item>
            <el-menu-item index="/Employees">公司人事</el-menu-item>
            <el-menu-item index="/Experts">专家介绍</el-menu-item>
            <el-menu-item index="/TimeLine">历史活动</el-menu-item>
          </el-menu>
          <div class="line"></div>
        </el-container>
      </el-header>
      <el-main>
        <router-view />
      </el-main>
    </el-container>

  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      isHid: false
    };
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath);
    },
    gotoHome () {
      location.href = '/';
    }
  }
};
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0;
}

@media screen and (min-width: 884px) {
  .logo {
    height: 100%;
    width: auto;
  }
  .logos {
    display: none;
  }
}

@media screen and (max-width: 884px) and (min-width: 628px) {
  .logo {
    display: none;
  }
  .logos {
    height: 100%;
    width: auto;
  }
}
@media screen and (max-width: 628px) {
  .logo {
    display: none;
  }
  .logos {
    display: none;
  }
}
.headerd {
  height: 64px;
  display: block;
  text-align: left;
  border-bottom: solid 1px #e6e6e6;
}

.el-menu-item {
  height: 100% !important;
}

.el-menu-item.is-active {
  /* border-bottom: 2px solid #409EFF; */
  border-bottom: 2px solid #67c23a !important;
}

.headlink {
  font-size: 58px;
}
.el-menu-demo {
  float: right;
  height: 100%;
}

.el-main {
  padding: 0 !important;
}

a:hover {
  color: #67c32a;
  text-decoration: underline;
}
a:link {
  color: #909399;
  text-decoration: none;
}
a:visited {
  color: #909399;
}
.el-message-box {
  width: auto !important;
}
</style>



