            '1. 2018 年 12 月29 日举行中国心胸血管麻醉学会心肺复苏委员会广西桂林市办事处成立大会，中国医师协会急诊医师分会黄子通副会长受聘办事处顾问，李小悦当选办事处主任，吕侣、陈丽娟、王永恒三位专家当选副主任，张玉奇、尹靖怡当选办事处秘书，20名专家组成学术委员会。同期举办心肺复苏培训班和“心脑血管急症科普进社区”活动。2019 年，继续急救科普社会化工作，立足基层，努力做好桂林市科普活动的布局和开展。',
            '2. 根据公安部 2019 年7 月 27 日印发的《关于切实加强现场培训急救训练工作的通知》有关要求，由公安部与中国心胸血管麻醉学会联合开展“心手相连，点亮生命”——现场急救训练进警营活动。经与广西壮族自治区公安厅沟通，由中国心胸血管麻醉学会心肺复苏委员会广西桂林市办事处承担广西壮族自治区范围内公安系统的培训工作。2019 年10 月12 日在广西南宁举行启动仪式。公安厅党委委员、政治部主任宾正迎，中国心胸血管麻醉学会广西桂林市办事处主任李小悦副教授等领导出席启动仪式。同期举办“心脑血管急症科普进社区”活动。',
            '3. 为进一步促进高质量心肺复苏技术在社区的推广、切实提示心脏骤停患者的院外抢救成功率，我们在心肺复苏技术社区培训过程中引入LinkCPR 人工按压质量反馈系统，运用该系统的实时CPR 质量监测和反馈功能，提高学员心肺复苏技术的培训效果、改善心肺复苏质量，切实提高心脑血管急症抢救成功率、降低致残率。本活动面向社区居民、志愿者及厂矿职工，将邀请有丰富临床经验的心脑血管急症领域专家进行宣教和培训，直接受益人口超过 10 万。',
            '4. 出版专著《心肺复苏学》1 部，立项自治区级教学研究与改革项目2 项。'
          ]
        },
        {
          id: 3,
          title: '2018 年“心脑血管急症科普进社区”活动总结',
          time: '2018/12/31',
          content: [
            '2018 年科普团队继续以“普及心脑血管急症知识、推广高质量心肺复苏术”为己任，依托社区、面向基层，开展一系列《心脑血管急症科普进社区》活动，取得良好的社会效益。',
            '1. 举办《心脑血管急症科普进社区》系列活动八场，足迹遍布临桂奥林匹克花园社区、山水凤凰城社区、两江机场、社会福利院、中建八局山水凤凰城项目部，参与活动人员超过 2000 人，包括青年学生、社区工作人员、社区居民、厂矿职工等，收益人群近 10 万人。',
            '2. 举办广西区医学继续教育项目《心血管急症与心肺脑复苏新进展》学习班暨第一届桂粤急危重症青年论坛，邀请来自广州、东莞、桂林、梧州、贺州等地从事心血管急症及心肺脑复苏领域的专家学者授课，近 200 名来自桂林市、柳州市、梧州市和贺州市的基层医务工作者参与，进一步扩大科普活动的宣传力度。',
            '3. 培养兼职科普宣讲人员 20 人。',
            '4. 出版《心肺脑复苏基础与临床》专著 1 部，第四篇中将科普团队在桂林开展心脑血管急症科普活动的经验归纳总结并进行推广。',
            '5. 为进一步推动桂林市心脑血管急症科普基地建设，项目负责人将《关于加强社区心脑血管急症科普基地建设的建议》作为九三学社桂林医学院支社代表提案上报九三学社市委会。',
            '6. 项目负责人当选中国民族医药学会科普分会常务委员、广西医师协会科普分会副主任委员、广西医学会科普分会委员；项目负责人牵头成立中国心胸血管麻醉学会心肺复苏全国委员会桂林办事处，并担任主任委员，进一步扩大桂林科普活动的影响力。'
          ]
        },
        {
          id: 2,
          title: '2017 年“心脑血管急症科普进社区”活动总结',
          time: '2017/9/9',
          content: [
            '为增强我镇中心小学教职员工对心脑血管急症的认知水平，进一步提高教职员工对急危重症的自救互救能力，根据今年“全国科普日”活动安排和我院科普活动计划，我院重症医学科于 9 月 21 日在塘厦中心小学成功举办《心脑血管急症科普进社区》讲座，与广大教职员工面对面宣传心脑血管急症和心肺复苏医学知识，共 80名教职员工参与理论授课和技能培训，覆盖人口 6000 人。',
            '全球所有的死亡原因数据调查中，据 WHO 世界卫生组织数据显示，在 2015年全球 5640 万例死亡中，半数以上（54%）由 10 个原因导致。缺血性心脏病和中风是世界最大的杀手，2016 年共造成 1500 万例死亡。这两种疾病在过去 15 年中一直是全球的主要死亡原因。以心血管急症中的心跳骤停为例，我国每年心跳骤停发生率为每年 54.4 人/10 万人，而仅有不到 1%患者存活，主要原因是我国目前医院外心跳骤停现场目击者实施心肺复苏的比例仅为 10%，也远低于欧美、日本等发达国家 90% 的水平。同样是属于心脑血管急症的急性脑梗死，由于错失溶栓“时间窗”而出现永久性肢体运动障碍的比例高达 70%以上，严重影响社区居民生活质量。',
            '心肺复苏技术是心脏骤停患者最简单有效的治疗手段。当发生心搏骤停时，旁观者如能在意外发生后及时实施有效的心肺复苏，可显著提高患者生存率。2015 年 AHA 心肺复苏指南和 ERC 心肺复苏指南要求对心脏骤停患者实施高质量心肺复苏（High Quality CPR，HQ-CPR），即：按压频率达到 100~120 次/分钟，按压深度大于 5cm。2013 年 AHA 专家共识指出，确保高质量 CPR、优化 CPR 质量、预防低质量 CPR 是提高心脏复苏效果的重点工作。要达到高质量心肺复苏，按压深度和按压频率的指示和反馈非常重要。目前心肺复苏技术的社区培训大多为理论授课、教员演示和对学员进行模拟环境下的操作考核，学员考核是否通过多由教员主观评价，缺乏标准和量化的评价指标，不利于学员真正掌握高质量的心肺复苏技术，不利于该项技术在社区的开展。为进一步促进高质量心肺复苏技术在社区的推广、切实提示心脏骤停患者的院外抢救成功率，我们在心肺复苏技术社区培训过程中引入 LinkCPR 人工按压质量反馈系统，运用该系统的实时 CPR 质量监测和反馈功能，提高学员心肺复苏技术的培训效果、改善心肺复苏质量，切实提高心脑血管急症抢救成功率、降低致残率。'
          ]
        },
        {
          id: 1,
          title: '2016 年急救科普活动总结',
          time: '2016/12/31',
          content: [
            '1. 2016 年 4 月 24 日东莞市塘厦医院重症医学科联合东莞东华医院重症医学科在塘厦镇社会事务大楼成功举办“心脑血管急症科普进社区”第一场讲座。',
            '2. 2016 年 4 月 26 日东莞市塘厦医院重症医学科联合东莞东华医院重症医学科在塘厦镇社会社区卫生服务中心成功举办“心脑血管急症科普进社区”第二场讲座。',
            '3. 2016 年 5 月 12 日东莞市塘厦医院重症医学科参加“塘厦镇 2016 年全国防灾减灾日现场活动”暨“心脑血管急症科普进社区”第三场讲座，为全镇机关、各社区人员讲授心肺复苏和止血包扎技术。',
            '4. 2016 年8 月 14 日东莞市塘厦医院主办 2015 年东莞市科普项目暨首届东莞东部急危重症论坛：东莞市东南部片区镇级医院和社区卫生服务中心急危重症系列讲座。',
            '5. 2016 年 9 月 17 日东莞市塘厦医院重症医学科联合东莞康华医院急诊科发在凤岗镇兴贸小区和中国邮政储蓄银行昌盛营业所成功举办“心脑血管急症科普进社区”第四场讲座。本讲座也是 2016 年全国科普日活动。'
          ]
        }]
    };
  }
};
