import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';
import PageNotFund from '@/components/404';
import Experts from '@/components/Experts';
import HistoryList from '@/components/TimeLine';
import Employees from '@/components/Employees';

Vue.use(Router);

export default new Router({
  routes: [{
    path: '/',
    redirect: '/Home'
  },
  {
    path: '/Home',
    name: '主页',
    component: Home,
    meta: { title: '恒悦健康管理' }
  },
  {
    path: '/404',
    name: '404',
    component: PageNotFund,
    meta: { title: '404页' }
  },
  {
    path: '/Experts',
    name: '专家介绍',
    component: Experts,
    meta: { title: '专家介绍' }
  },
  {
    path: '/TimeLine',
    name: '历史活动',
    component: HistoryList,
    meta: { title: '历史活动' }
  },
  {
    path: '/Employees',
    name: '公司人事',
    component: Employees,
    meta: { title: '公司人事' }
  },
  {
    path: '*',
    redirect: '/404'
  }]
});



